.signupForm {
  text-align: center;
  padding: 5%;
}

label {
  display: block;
  letter-spacing: 4px;
  padding-top: 20px;
  text-align: center;
}
label .label-text {
  color: white;
  cursor: text;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  -ms-transform: translateY(-34px);
  -webkit-transform: translateY(-34px);
  transform: translateY(-34px);
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
label input {
  background-color: transparent;
  border: 0;
  border-bottom: 2px solid white;
  color: white;
  font-size: 23px;
  letter-spacing: -1px;
  outline: 0;
  padding: 5px 20px;
  text-align: center;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  width: 200px;
}
label input:focus {
  max-width: 100%;
  width: 400px;
}
label input:focus + .label-text {
  color: #F0F0F0;
  font-size: 13px;
  -ms-transform: translateY(-74px);
  -webkit-transform: translateY(-74px);
  transform: translateY(-74px);
}
label input:valid + .label-text {
  font-size: 13px;
  -ms-transform: translateY(-74px);
  -webkit-transform: translateY(-74px);
  transform: translateY(-74px);
}

button {
  background-color: white;
  border: 2px solid white;
  border-radius: 27px;
  color: #fd4f33;
  cursor: pointer;
  font-size: 20px;
  margin-top: 20px;
  padding: 15px 30px;
  text-transform: uppercase;
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
button:hover, button:focus {
  background-color: white;
  color: #333333;
  outline: 0;
}

.soundifySignup {
  color: white;
  text-align: center;
  font-size: 60px;
/*  padding-bottom: -15%;*/
  letter-spacing: 6px;
}

.signupSubmit {
  text-decoration: none;
}




